import React from "react";

const mapTypeToIconName = {
  temperature: "temperature",
  discharge: "drain",
  sealevel: "level",
  level: "level",
  dischargeliter: "drain"
};

const mapTypeToScale = {
  temperature: "°C",
  discharge: "m³/s",
  sealevel: "m.ü.M.",
  level: "m",
  dischargeliter: "l"
};

const Measurement = ({ type, values }) => {
  if (values[0]) {
    return (
      <div className="measurement-container">
        <div className="measurement-box">
          <div className="value">{values[0].value}</div>
          <div className="scale">
            <div className="measurementIcon">
              <img
                src={require(`../../assets/icons/${
                  mapTypeToIconName[type]
                }.svg`)}
                height={25}
                width={25}
                alt={`weather_icon_${type}`}
              />
            </div>
            <div className="measurand">{mapTypeToScale[type]}</div>
          </div>
        </div>
      </div>
    );
  } else {
    return null;
  }
};

export default Measurement;



