import React from "react";

import Measurement from "./components/measurement";
import "./style.css";

class Station extends React.Component {
  state: {
    stationHistory: null,
    station: null
  };

  componentDidMount() {
    let stationId = this.props.match.params.id;

    const host = process.env.REACT_APP_HOSTNAME || 'http://localhost:3000';

    fetch(`${host}/api/stations/${stationId}`)
      .then(response => response.json())
      .then(json => {
        this.setState({
          station: json
        });
      });
  }

  render() {
    if (this.state) {
      const measurementKeys = ["temperature", "discharge", "sealevel", "level", "dischargeliter"];

      return (
        <div>
          <div className="station-header">
            <p>{this.state.station.name}</p>
            <h4>{this.state.station.waterBodyName}</h4>
          </div>
          <div>
            {measurementKeys.map(measurement => {
              if(this.state.station.water[measurement] && this.state.station.water[measurement].values) {
                return (
                  <Measurement
                    key={measurement}
                    type={measurement}
                    values={this.state.station.water[measurement].values}
                  />
                );
              } else {
                return <div key={measurement}>{measurement}</div>;
              }
            })}
          </div>
        </div>
      );
    } else {
      return (
        <div className="spinner">
          <div className="double-bounce1" />
          <div className="double-bounce2" />
        </div>
      );
    }
  }
}

export default Station;



