const R = require("ramda");

const search = (state, userProvidedSearchTerm) => {
  let searchTerm = userProvidedSearchTerm.toLowerCase();

  if (!userProvidedSearchTerm || !R.trim(userProvidedSearchTerm))
    return { ...state, searchTerm, searchResults: [] };

  let results = state.stations.filter(station => {
    return (
      station.name.toLowerCase().includes(searchTerm) ||
      station.waterBodyName.toLowerCase().includes(searchTerm)
    );
  });

  return {
    ...state,
    searchTerm,
    searchResults: results
  };
};

export default {
  state: {
    stations: [],
    searchTerm: "",
    searchResults: []
  },
  reducers: {
    setStations: (state, payload) => ({
      ...state,
      stations: payload
    }),
    search
  },
  effects: {
    async getStations(rootState) {
      const host = process.env.REACT_APP_HOSTNAME || 'http://localhost:3000';
      const response = await fetch(`${host}/api/stations`);
      const data = await response.json();
      this.setStations(data);
    }
  }
};



