import React from "react";
import { connect } from "react-redux";

import Routes from "../routes";

const mapState = ({ main }) => ({
  stations: main.stations,
  searchTerm: main.searchTerm,
  searchResults: main.searchResults
});

const mapDispatch = ({ main }) => {
  return {
    getStations: () => main.getStations(),
    search: event => main.search(event.target.value),
    switchToStationView: stationId => _e => {
      Routes.switchToStationView(stationId);
    }
  };
};

class Main extends React.Component {
  componentDidMount() {
    this.props.getStations();
  }

  render() {
    return (
      <div className="container">
        <input
          type="text"
          className="search"
          onChange={this.props.search}
          autoFocus="true"
          placeholder="Fluss, Gewässer, Ort"
        />
        <div className="searchResults">
          {this.props.searchResults.map(station => {
            return (
              <div
                key={station.id}
                className="station"
                onClick={this.props.switchToStationView(station.id)}
              >
                <div>{station.waterBodyName}</div>
                <div className="location">{station.name}</div>
              </div>
            );
          })}
        </div>
      </div>
    );
  }
}

export default connect(mapState, mapDispatch)(Main);



